import React from 'react';
import { motion } from 'framer-motion';
import { ArrowRightIcon, CheckIcon } from '@heroicons/react/24/outline';
import { useLanguage } from '../context/LanguageContext';
import { Helmet } from 'react-helmet-async';

const SynapsisPricing = () => {
  const { t } = useLanguage();

  // Helper to scroll smoothly to the contact section
  const scrollToContact = () => {
    const contactSection = document.getElementById('contact');
    if (contactSection) {
      contactSection.scrollIntoView({ behavior: 'smooth' });
    } else {
      // Fallback in case the section is not found
      window.location.hash = 'contact';
    }
  };

  // Planes de precios
  const pricingPlans = [
    {
      name: t('pricing.basic.name', 'Bsico'),
      description: t('pricing.basic.description', 'Pgina web esttica perfecta para pequeas empresas y profesionales'),
      price: t('pricing.basic.price', '250000'),
      currency: '$',
      period: t('pricing.period.yearly', '/ao'),
      features: [
        t('pricing.basic.feature1', 'Diseo Responsive'),
        t('pricing.basic.feature2', 'Hasta 5 pginas'),
        t('pricing.basic.feature3', 'Formulario de contacto'),
        t('pricing.basic.feature4', 'SEO bsico'),
        t('pricing.basic.feature5', 'Integracin con redes sociales'),
      ],
      cta: t('pricing.basic.cta', 'Comenzar ahora'),
      popular: false,
      color: 'from-cyan-500 to-blue-500',
      href: '#contact'
    },
    {
      name: t('pricing.pro.name', 'Profesional'),
      description: t('pricing.pro.description', 'Sitio web dinmico con CMS para empresas en crecimiento'),
      price: t('pricing.pro.price', '1299'),
      currency: '$',
      period: t('pricing.period.yearly', '/ao'),
      features: [
        t('pricing.pro.feature1', 'Todo lo del plan Bsico'),
        t('pricing.pro.feature2', 'Hasta 15 pginas'),
        t('pricing.pro.feature3', 'Panel de administracin'),
        t('pricing.pro.feature4', 'Blog integrado'),
        t('pricing.pro.feature5', 'SEO avanzado'),
        t('pricing.pro.feature6', 'Optimizacin de velocidad'),
      ],
      cta: t('pricing.pro.cta', 'Seleccionar plan'),
      popular: true,
      color: 'from-fuchsia-500 to-purple-600',
      href: '#contact'
    },
    {
      name: t('pricing.enterprise.name', 'Empresarial'),
      description: t('pricing.enterprise.description', 'Solucin completa con funcionalidades avanzadas para grandes empresas'),
      price: t('pricing.enterprise.price', '2499'),
      currency: '$',
      period: t('pricing.period.yearly', '/ao'),
      features: [
        t('pricing.enterprise.feature1', 'Todo lo del plan Profesional'),
        t('pricing.enterprise.feature2', 'Pginas ilimitadas'),
        t('pricing.enterprise.feature3', 'E-commerce integrado'),
        t('pricing.enterprise.feature4', 'Pasarela de pagos'),
        t('pricing.enterprise.feature5', 'rea de clientes'),
        t('pricing.enterprise.feature6', 'Anlisis avanzado'),
        t('pricing.enterprise.feature7', 'Soporte prioritario 24/7'),
      ],
      cta: t('pricing.enterprise.cta', 'Contactar ventas'),
      popular: false,
      color: 'from-indigo-500 to-indigo-800',
      href: '#contact'
    }
  ];

  // Estructura de datos Schema.org para los planes de precios
  const structuredData = {
    "@context": "https://schema.org",
    "@type": "ItemList",
    "itemListElement": pricingPlans.map((plan, idx) => ({
      "@type": "Product",
      "position": idx + 1,
      "name": plan.name,
      "description": plan.description,
      "offers": {
        "@type": "Offer",
        "price": plan.price,
        "priceCurrency": plan.currency === '$' ? 'USD' : plan.currency,
        "availability": "https://schema.org/InStock"
      }
    }))
  };

  return (
    <section id="pricing" className="py-24 px-4 relative overflow-hidden">
      {/* Schema.org structured data for pricing */}
      <Helmet>
        <script type="application/ld+json">{JSON.stringify(structuredData)}</script>
      </Helmet>
      {/* Background Effects */}
      <div className="absolute inset-0">
        <div className="absolute inset-0 grid-pattern opacity-10" />
        <div className="absolute top-0 left-0 w-full h-full bg-gradient-to-b from-transparent via-purple-500/10 to-transparent" />
      </div>

      <div className="max-w-7xl mx-auto relative z-10">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl font-bold mb-6">
            {t('pricing.title', 'Nuestros')} <span className="text-transparent bg-clip-text bg-gradient-to-r from-fuchsia-400 to-cyan-400">{t('pricing.titleHighlight', 'Planes')}</span>
          </h2>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            {t('pricing.subtitle', 'Soluciones a medida para cada etapa de tu negocio')}
          </p>
        </motion.div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {pricingPlans.map((plan, index) => (
            <motion.div
              key={index}
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
              viewport={{ once: true }}
              className={`glass-effect rounded-2xl overflow-hidden ${plan.popular ? 'ring-2 ring-fuchsia-500 transform scale-105 md:-my-4' : ''}`}
            >
              {plan.popular && (
                <div className="bg-gradient-to-r from-fuchsia-500 to-purple-600 text-white text-sm font-semibold text-center py-2">
                  {t('pricing.mostPopular', 'Ms popular')}
                </div>
              )}
              
              <div className="p-8">
                <div className="text-center mb-8">
                  <h3 className="text-2xl font-bold text-white mb-2">{plan.name}</h3>
                  <p className="text-gray-400 mb-6">{plan.description}</p>
                  <div className="flex items-baseline justify-center gap-1">
                    <span className="text-gray-400 text-xl">{plan.currency}</span>
                    <span className={`text-5xl font-bold bg-gradient-to-r ${plan.color} bg-clip-text text-transparent`}>{plan.price}</span>
                    <span className="text-gray-400 text-sm">{plan.period}</span>
                  </div>
                </div>
                
                <div className="space-y-4 mb-8">
                  {plan.features.map((feature, idx) => (
                    <div key={idx} className="flex items-start gap-3">
                      <div className={`flex-shrink-0 p-1 bg-gradient-to-r ${plan.color} rounded-full mt-1`}>
                        <CheckIcon className="w-4 h-4 text-white" />
                      </div>
                      <span className="text-gray-300">{feature}</span>
                    </div>
                  ))}
                </div>
                
                <motion.button
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  className={`w-full py-3 rounded-xl bg-gradient-to-r ${plan.color} text-white font-semibold flex items-center justify-center gap-2`}
                  onClick={scrollToContact}
                >
                  {plan.cta}
                  <ArrowRightIcon className="w-4 h-4" />
                </motion.button>
              </div>
            </motion.div>
          ))}
        </div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.5 }}
          viewport={{ once: true }}
          className="mt-16 text-center"
        >
          <p className="text-white">
            {t('pricing.customSolution', 'Necesitas una solucin personalizada?')} {' '}
            <a href="#contact" className="font-medium text-fuchsia-400 hover:text-fuchsia-300 underline">
              {t('pricing.contactUs', 'Contctanos')}
            </a>
          </p>
        </motion.div>
      </div>
    </section>
  );
};

export default SynapsisPricing; 